

.card {
    display: flex;
    flex-direction: column;
     align-items: center;
     justify-content: center;
     margin-top: 8%;
}

.card>p{
    font-style: italic;
}
.college{
    font-weight: bold;
}

.loginForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  padding: 2vmax;
  justify-content: space-evenly;
  height: 70%;
  transition: all 0.5s;
}

.loginForm > div > input{
  padding: 1vmax 4vmax;
  padding-right: 1vmax;
  width: 100%;
  box-sizing: border-box;
  margin: 0.7vmax;
  border: 1px solid rgba(0, 0, 0, 0.267);
  border-radius: 4%;
  font: 300 0.9vmax cursive;
  outline: none;
  color:rgba(0, 0, 0, 0.816);
}

.loginBtn {
    border: none;
    background-color:rgba(167, 167, 169, 0.7);
    color: rgb(12, 12, 12);
    font: 300 1.1vmax "Roboto";
    width: 8vmax;
    padding: 0.8vmax;
    transition: all 0.5s;
    border-radius: 4%;
    outline: none;
    margin: 2vmax;
 /*   box-shadow: var(--box-shadow);*/
  }

  .loginBtn:hover {
    background-color: rgb(132, 129, 129);
    cursor: pointer;
  } 

  .icons{
    transform: translateY(-200%);
  }