.dropdown-admin{
    display: flex;
    align-items: center;
    margin-left: 1vmax;
    margin-right: 1vmax;
    left:79vmax;
    top:1.5vmax;
    position: absolute;
    font-weight: bold;
  }
  .dropdown-admin:hover{
    cursor: pointer;

    transition: all 0.2s ease-out;
  }


  .dropdown-menu-admin {
    position: absolute;
    left:77vmax;
    background-color: #827f7f00; /* Change to your desired background color */
   /* border: 1px solid #ccc; /* Change to your desired border color */
   /* box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);/* Add shadow for a dropdown effect */
    list-style: none;
    padding: 0;
    min-width: 160px;
  /*  min-height: 10vmax;*/
    z-index: 1;
  }
  
  .dropdown-menu-admin >li {
    padding-bottom: 0.8vmax;
    overflow: hidden;
   /* margin-bottom: 1vmax 0; /* 1vmax margin above and below each item */
   z-index: 1;
  
  }
  
  .dropdown-menu-admin >li>a {
    text-decoration: none;
    color: rgb(89, 87, 87);
    padding-bottom: 1rem;
    overflow: hidden;
    margin-bottom: 1vmax 0; /* 1vmax margin above and below each item */
    padding:0.5vmax;
    font-weight: bold;
    transition: all 0.2s ease-out;
  }

  .dropdown-menu-admin >li>a:hover{
    background-color: #6b6b6b5e;
    color:rgb(59, 57, 107);
    font-weight: bold;
    border-radius: 8%;
  }
  .first{
    padding-top: 1.5vmax;
  }