
.productListContainer{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 13vmax;
    margin-top: 2vmax;
}

.searchBox{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 3vmax;
    margin-top: 1.5vmax;
}

.searchType{
    background-color: fff;
    padding: 0.4vmax 0vmax;
    font-size: 1vmax;
    min-width: 4vmax;
    font-style: italic;
    align-items: center;
    justify-content: center;
    display: flex;
    cursor: pointer;
    color:rgb(39, 39, 39);
    border:none;
}



.searchInput{
    height:1.72vmax;
    border: 1px solid rgb(192, 188, 188);
    min-width: 14vmax;
}

.searchForm{
    display: flex;
}


.searchBtn {
  background-color: #13aa52;
  border: none;
  color: #fff;
  cursor: pointer;
  font-family: "Akzidenz Grotesk BQ Medium", -apple-system, BlinkMacSystemFont, sans-serif;
  font-size: 1vmax;
  font-weight: 400;
  outline: none;
  outline: 0;
  padding: 0.4vmax;
  text-align: center;
  transition: all linear 0.2s;
}

.searchBtn:hover {
  box-shadow: rgba(0, 0, 0, .15) 0 3px 9px 0;
  background-color: #2cd572;
}


.headings{
    font-size: 1vmax;
    font-family: cursive;
    color: rgba(29, 28, 28, 0.86);
}

.rlinks{
  text-decoration: none;
}

.cellData{
    font-size: 0.9vmax;
    font-family:Arial, Helvetica, sans-serif;
    font-weight: 400;
}

.cellLink{
    text-decoration: none;
    font-size: 1vmax;
    color:#367AF6;
    font-style: italic;
    font-weight: bold;
}

.cellLink:hover{
    color:#0742ae;
}

.btn {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0.25vmax 1.3vmax;
  font-family: Arial, Helvetica, sans-serif;
  border-radius: 0.6vmax;
  border: none;
 /* line-height: 1.15;*/
  color: #fff !important;
  font-size: 0.9vmax;
  /*background: linear-gradient(180deg, #4B91F7 0%, #367AF6 100%);*/
  background-color: #367AF6;
   background-origin: border-box;
  box-shadow: 0px 0.05vmax 0.15vmax rgba(54, 122, 246, 0.25), inset 0px 0.08vmax 0px -0.025vmax rgba(255, 255, 255, 0.2);
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.btn:hover {
    transition: all .5s ease;
    box-shadow: 0 0 0 0 #656fde, 0 0 0 0.1vmax #656fde;
    background-color: #656fde;
    outline: 0;
    cursor: pointer;
}


.delBtn{
    
   
    display: inline-block;
    outline: none;
    cursor: pointer;
    font-size: 0.9vmax;
    padding: 0 1vmax;
    height: 2vmax;
    max-height: 2vmax;
    background: none;
    font-weight: 700;
    border: 0;
    border-radius: 0;
    color: #D92736;
    transition-timing-function: ease-in-out;
    transition-duration: 150ms;
    box-shadow: none;

}

.delBtn:hover{
    background: #F7F9FD;
}

.redClass{
    color:#ea5353de !important;
}

.greenClass{
    color:#19c021 !important;
}
