.container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
   /* margin-top: 7%;*/
}

.container > img{
    margin:1.5vmax;
    height: 15vmax;
}

.container > div{
    margin: 1vmax;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: rgb(59, 59, 59);
    font-size: 1.75vmax;
    font-weight: bold;
 
}

.container > div > span{
    font-weight: bolder;
    margin-top: 1vmax;
    font-size: 1.5vmax;
    color: rgb(113, 104, 104);
    font-family: 'Courier New', Courier, monospace;
}

.info{
    font-family: cursive !important;
    font-size: 1.25vmax !important;
}

.blueClass{
    color: rgb(63, 63, 231) !important;
}

