.logo{
  height: 3vmax;
  width: 3vmax;
}

.navbar {
  background: linear-gradient(90deg, rgba(28, 27, 27, 0) 0%, rgba(255, 255, 255, 0) 100%);
  height: 4vmax;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
}

.navbar-logo {
  color: #1a1a1a90;
  justify-self: start;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
}

.fa-firstdraft {
  margin-left: 0.5rem;
  font-size: 1.6rem;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(5, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 70vw;
  justify-content: end;
  margin-right: 2rem;
}

.nav-Home {
  display: flex;
  align-items: center;
  margin-left: 1vmax;
  margin-right: 1vmax;
  left:65vmax;
  top:1vmax;
  position: absolute;
}

.nav-about {
  display: flex;
  align-items: center;
  margin-left: 1vmax;
  margin-right: 1vmax;
  left:91vmax;
  top:1vmax;
  position: absolute;
}

.nav-links {
  color: rgb(33, 33, 33);
  text-decoration: none;
  padding: 0.5rem 1rem;
  font-weight: bold;
}

.nav-links:hover {
  background-color: #6b6b6b5e;
  color:rgb(59, 57, 107);
  border-radius: 4px;
  transition: all 0.2s ease-out;

}

.fa-bars {
  color: #242424;
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}

